import React from 'react'

import { FaFacebook ,FaLinkedinIn,FaInstagram,FaGoogle,FaWhatsapp} from "react-icons/fa";
import {IconContext} from 'react-icons'
function Contact(){
    return(

      <div className="contact">
        <h1>contact</h1>
        <div className="contact-style">
          <IconContext.Provider value={{size:"5rem"}} >

         <FaFacebook id="f" />
          <FaLinkedinIn id="l"  />
          <FaInstagram id="I"/>
          <FaGoogle id="G" />
          <FaWhatsapp id="W" />
          </IconContext.Provider>
        </div>
      </div>
    )
}
export default Contact